import React from 'react';
import withBem from 'hoc/withBem';
import { throttle } from 'tools';
import { updateUser, makePurchase } from 'store/actions';
import { AppDispatch } from 'store/constants';
import { useDispatch, useSelector } from 'hooks';
import { Button } from 'components/Button/Button';

import './CartInfo.scss';
import { MINIMAL_PRICE } from '../Cart';

type CartInfoProps = BemProps;

const makePurchaseAction = (dispatch: AppDispatch) => {
    dispatch(makePurchase());

    //       
    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line @typescript-eslint/camelcase
            smart_app_data: {
                command: 'VALIDATION_OK',
                status: true,
                // eslint-disable-next-line @typescript-eslint/camelcase
                order_id: '364528',
            },
        });
    }
};

const Component: React.FC<CartInfoProps> = ({ bem }) => {
    const dispatch = useDispatch();
    const { user, cart } = useSelector(({ user, cart }) => ({ user, cart }));
    const {
        // eslint-disable-next-line @typescript-eslint/camelcase
        phone_num,
        // eslint-disable-next-line @typescript-eslint/camelcase
        address: { address_string, building, confirmed },
        // eslint-disable-next-line @typescript-eslint/camelcase
        customer_name,
    } = user;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const phoneNumber = phone_num;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const name = customer_name;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const addressString = address_string;

    return (
        <div className={bem()}>
            <div className={bem('badge')}>
                <div className={bem('badge-body')}>
                    <div className={bem('badge-name')}>15-30</div>
                    <div className={bem('badge-desc')}></div>
                </div>
                <div className={bem('badge-kernel')} />
                <div className={bem.modify({ big: true }, 'badge-shadow')} />
                <div className={bem.modify({ small: true }, 'badge-shadow')} />
            </div>
            <div className={bem('address')}>{addressString}</div>
            {confirmed ? (
                <>
                    <div className={bem('personal')}>
                        <div className={bem('personal-location')}>{building}</div>
                        <div className={bem('personal-phone-number')}>{phoneNumber}</div>
                        <div className={bem('personal-name')}>{name}</div>
                    </div>
                    <Button
                        className={bem.modify({ disabled: false }, 'button')}
                        disabled={cart.totalPrice < MINIMAL_PRICE}
                        shouldFocusOnMount
                        onClick={throttle(() => makePurchaseAction(dispatch), 200)}
                    >
                          
                    </Button>
                </>
            ) : (
                <>
                    <div className={bem('ask-personal')}>
                          ,  ,     
                    </div>
                    <Button
                        className={bem.modify({ disabled: false }, 'button')}
                        shouldFocusOnMount
                        onClick={throttle(
                            () =>
                                dispatch(
                                    updateUser({
                                        location: ' 1,  ',
                                        phoneNumber: '+7 910 345 67 89',
                                        name: '',
                                    }),
                                ),
                            200,
                        )}
                    >
                         
                    </Button>
                </>
            )}
        </div>
    );
};

export const CartInfo = withBem(Component, 'CartInfo');
